@charset "UTF-8";
/* COLOR VARIABLES */
/* FONT SIZE VARIABLES */
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

@font-face {
  font-family: "wild-icons";
  src: url("../fonts/wild-icons.eot");
  src: url("../fonts/wild-icons.eot?#iefix") format("eot"), url("../fonts/wild-icons.woff2") format("woff2"), url("../fonts/wild-icons.woff") format("woff"), url("../fonts/wild-icons.ttf") format("truetype"), url("../fonts/wild-icons.svg#wild-icons") format("svg");
}

.icon-search:before {
  font-family: "wild-icons";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  text-decoration: none;
  text-transform: none;
}

.icon-search:before {
  content: "ÓÄÅ";
}

body {
  font-family: "Open Sans", sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

.nav {
  display: flex;
  flex: 1 1 auto;
  justify-content: space-between;
  align-items: center;
}

.nav__menu {
  display: flex;
  flex: 1 0 auto;
  justify-content: space-around;
  list-style: none;
}

.nav__link {
  display: block;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  transition-duration: 0.2s;
}

.nav__link:hover {
  transform: scale(1.2);
}

.nav__search {
  font-size: 1.3em;
}

.button {
  display: inline-block;
  padding: 0.7em 2em;
  border: 2px solid #ffffff;
  border-radius: 2em;
  background-color: rgba(255, 255, 255, 0.2);
  font-family: inherit;
  font-size: inherit;
  line-height: 1em;
  color: inherit;
  transition-duration: 0.2s;
}

.button:hover {
  background-color: rgba(255, 255, 255, 0.4);
  cursor: pointer;
}

.header__title, .header__subtitle {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
}

.header {
  height: 100vh;
  box-sizing: border-box;
  background-color: #95a9b1;
  padding: 50px;
  font-size: 25px;
  color: #ffffff;
}

.header__top {
  display: flex;
  align-items: center;
  padding: 2em;
  background-color: #ffffff;
  font-size: 16px;
  color: #c1b2b1;
}

.header__logo {
  flex: 2 auto;
}

.header__nav {
  flex: 1 auto;
}

.header__greating {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.header__title {
  padding-top: 10px;
  padding-bottom: 20px;
  font-size: 100px;
  letter-spacing: 0.1em;
}

.header__subtitle {
  letter-spacing: 0.2em;
  font-family: "Raleway", sans-serif;
}
